<template>
    <div id="app">
        <video :src="api + video.url" v-if="video" class="video" controls="controls"></video>
    </div>
</template>

<script>

import Vue from 'vue';
const server = '';
Vue.filter('img', function(img){
    if(!img){
        return null;
    }
    return server + img;
})

export default {
	name: 'App',

    data(){
        return {
            id: null,
            token: null,
            video: null,
            api: `${process.env.URL_PRE}`
        }
    },
    mounted(){
        const params = this.findParams();
        this.id = params.id;
        this.token = params.token;

        if(!this.id){
            return ;
        }

        this.load();
        this.loadWXCfg();

    },

    methods: {
        findParams(){
            const params = {};
            let search = window.location.search;
            if(search){
                search = search.substring(1);
                const paramPair = search.split('&');

                for(let str of paramPair){
                    const pair = str.split('=');
                    params[pair[0]] = pair[1];
                }
            }
            return params;
        },

        // 加载数据
        load(){
            $.post(`${process.env.URL_PRE}/videoApi/getVideoInfo`, {
                vid: this.id,
                token: this.token, //'t+/JkYyUZ9jyZhW3OHbMEVxfyHd9gyeshF7s6oTxRIg06cAdg6/60BYHDzE='
            }, (resp)=>{
                if(!resp.code){
                    this.video = resp.result.data;
                    this.loaded = true;
                    this.initShare();

                    document.title = this.video.title || '视频';
                }
            });
        },

        loadWXCfg: function(){
            ///wxApi/shareConfig
            $.post('/wxApi/shareConfig', null, (resp)=>{
                const result = resp.result[0];
                wx.config({
                    // debug: true,
                    appId: result['appId'],
                    timestamp: result['timestamp'],
                    nonceStr: result['nonceStr'],
                    signature: result['signature'],
                    jsApiList: ['onMenuShareTimeline', 'onMenuShareAppMessage', 'onMenuShareQQ', 'onMenuShareWeibo', 'onMenuShareQZone']
                });
                const that = this;
                wx.ready(()=>{
                    that.wxCfged = true;
                    that.initShare();
                });
                wx.error(function(res){
                    console.log(res);
                })
            });
        },

        // 渲染
        render(video){
            document.title = video.title;
        },


        initShare(){
            if(!this.loaded || !this.wxCfged){
                return ;
            }

            const link = window.location.href.replace(/#\S*/,"");
            const title = this.video.title;
            const desc = this.video.share_content || '新款发布';
            const imgUrl = window.location.origin + (this.video.share_icon || '/static/albums/album-icon.png');
            // 分享到朋友圈
            wx.onMenuShareTimeline({
                title: desc, // 分享标题
                link: link, // 分享链接
                imgUrl: imgUrl, // 分享图标
                success: function () {
                },
                cancel: function () {}
            });

            // 分享给朋友
            wx.onMenuShareAppMessage({
                title: title, // 分享标题
                desc: desc, // 分享描述
                link: link, // 分享链接，该链接域名必须与当前企业的可信域名一致
                imgUrl: imgUrl, // 分享图标
                type: 'link', // 分享类型,music、video或link，不填默认为link
                success: function () {

                },
                cancel: function () {

                }
            });

            wx.onMenuShareQQ({
                title: title, // 分享标题
                desc: desc, // 分享描述
                link: link, // 分享链接
                imgUrl: imgUrl, // 分享图标
                success: function () {
                },
                cancel: function () { }
            });

            wx.onMenuShareWeibo({
                title: title, // 分享标题
                desc: desc, // 分享描述
                link: link, // 分享链接
                imgUrl: imgUrl, // 分享图标
                success: function () {
                },
                cancel: function () { }
            });

            wx.onMenuShareQZone({
                title: title, // 分享标题
                desc: desc, // 分享描述
                link: link, // 分享链接
                imgUrl: imgUrl, // 分享图标
                success: function () {
                },
                cancel: function () { }
            });
        }

    }
}
</script>

<style>
html,body,#app{
    width:100%;
    height:100%;
    padding:0;
    margin:0;
    overflow:hidden;
}
#app{
    position:fixed;
    top:0;
    left:0;
    right:0;
    bottom:0;
}
.video{
    width:100%;
    height:100%;
}
</style>



